const _ = require('lodash');

const _providers = [];

module.exports = {
  get: (path, ...opts) => {
    for (const provider of _providers) {
      return provider.get(path, ...opts);
    }
    // use default vars from process
    return _.get(process.env, path);
  },
  use: (provider) => {
    if (_.indexOf(_providers, provider) === -1) {
      _providers.push(provider);
    }
  }
};
